<template>
  <div id="webinar">
    <transition name="fade">
      <div class="" v-if="menus.submenu == ''">
        <div class="row my-4">
          <div class="col-md-6 text-start">
            <input type="text" class="form-mentoring" placeholder="Search" />
          </div>
          <div class="col-md-6 text-md-end text-center">
            <button
              class="btn-mentoring btn-type-2"
              @click="this.$router.push({ path: '/admin/webinar/add' })"
            >
              Add a New Webinar
            </button>
          </div>
        </div>
        <div class="card-white">
          <div class="table-responsive">
            <table class="table align-middle table-hover">
              <thead>
                <tr class="text-center">
                  <th>No</th>
                  <th>Webinar Name</th>
                  <th>Date & Time</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  class="text-center pointer"
                  v-for="i in 5"
                  :key="i"
                  @click="
                    this.$router.push({ path: '/admin/webinar/detail/' + i })
                  "
                >
                  <td>{{ i }}</td>
                  <td>Lorem Ipsum</td>
                  <td>20 Feburary 2022</td>
                </tr>
              </tbody>
            </table>
          </div>
          <nav class="mt-2">
            <ul class="pagination justify-content-center">
              <li class="page-item" v-if="webinars.current_page != 1">
                <a class="page-link" @click="getPage(webinars.links[0].url)">
                  <i class="fa-solid fa-chevron-left"></i>
                </a>
              </li>
              <div v-for="i in webinars.last_page" :key="i">
                <li
                  class="page-item"
                  v-if="
                    webinars.current_page - 2 < i &&
                    webinars.current_page + 2 > i
                  "
                >
                  <a
                    class="page-link"
                    :class="
                      webinars.current_page == i ? 'bg-primary text-white' : ''
                    "
                    href="#"
                    @click="getPage(webinars.path + '?page=' + i)"
                    >{{ i }}</a
                  >
                </li>
              </div>
              <li
                class="page-item"
                v-if="webinars.current_page != webinars.last_page"
              >
                <a class="page-link" @click="getPage(webinars.next_page_url)">
                  <i class="fa-solid fa-chevron-right"></i>
                </a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </transition>

    <transition name="fade">
      <v-detail
        :menus="menus"
        v-if="menus.menu == 'webinar' && menus.submenu != ''"
      ></v-detail>
    </transition>
  </div>
</template>

<script>
import Detail from "@/components/admin/webinar/detail";

export default {
  name: "webinar",
  props: {
    menus: Object,
  },
  components: {
    "v-detail": Detail,
  },
  data() {
    return {
      webinars: [],
    };
  },
  methods: {
    getData() {
      this.$axios
        .get(this.$url + "list/activities/webinar", {
          headers: {
            Authorization: "Bearer " + this.$adminToken,
          },
        })
        .then((response) => {
          this.webinars = response.data.data;
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    getPage(link) {
      this.$axios
        .get(link, {
          headers: {
            Authorization: "Bearer " + this.$adminToken,
          },
        })
        .then((response) => {
          this.webinars = response.data.data;
          // console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  created() {
    this.getData();
  },
};
</script>

